import React from "react";
import "./HeaterKit.css";

const pads = [
  {
    keyTrigger: "Q",
    title: "Heater-1",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Heater-1.mp3",
    style: {},
  },
  {
    keyTrigger: "W",
    title: "Heater-2",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Heater-2.mp3",
    style: {},
  },
  {
    keyTrigger: "E",
    title: "Heater-3",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Heater-3.mp3",
    style: {},
  },
  {
    keyTrigger: "A",
    title: "Heater-4",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Heater-4_1.mp3",
    style: {},
  },
  {
    keyTrigger: "S",
    title: "Clap",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Heater-6.mp3",
    style: {},
  },
  {
    keyTrigger: "D",
    title: "Open-HH",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Dsc_Oh.mp3",
    style: {},
  },
  {
    keyTrigger: "Z",
    title: "Kick-n'-Hat",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Kick_n_Hat.mp3",
    style: {},
  },
  {
    keyTrigger: "X",
    title: "Kick",
    url: "https://s3.amazonaws.com/freecodecamp/drums/RP4_KICK_1.mp3",
    style: {},
  },
  {
    keyTrigger: "C",
    title: "Closed-HH",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Cev_H2.mp3",
    style: {},
  },
];
const onStyle = {
  boxShadow: "0 9px 20px rgb(0, 0, 255)",
  animation: "test 10s infinite",
  color: "rgb(0, 0, 255)",
};

const offStyle = {
  boxShadow: "0 9px 20px rgb(74, 74, 75)",
  animation: "none",
  color: "rgb(74, 74, 75)",
};

const heaterKit = (props) => {
  const keyPads = pads.map((pad) => {
    return (
      <button
        key={pad.title}
        className="button"
        style={props.currentPower ? onStyle : offStyle}
        onClick={props.currentPower ? () => {
          new Audio(pad.url).play();
          props.padKeyTrigger(pad.title);
        } : null}
      >
        {pad.keyTrigger}
      </button>
    );
  });

  return <div className="wrapper">{keyPads}</div>;
};

export default heaterKit;
