const pads = [
  {
    keyTrigger: "Q",
    title: "Chord-1",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Chord_1.mp3",
    style: {},
  },
  {
    keyTrigger: "W",
    title: "Chord-2",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Chord_2.mp3",
    style: {},
  },
  {
    keyTrigger: "E",
    title: "Chord-3",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Chord_3.mp3",
    style: {},
  },
  {
    keyTrigger: "A",
    title: "Shaker",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Give_us_a_light.mp3",
    style: {},
  },
  {
    keyTrigger: "S",
    title: "Open-HH",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Dry_Ohh.mp3",
    style: {},
  },
  {
    keyTrigger: "D",
    title: "Closed-HH",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Bld_H1.mp3",
    style: {},
  },
  {
    keyTrigger: "Z",
    title: "Punchy-Kick",
    url: "https://s3.amazonaws.com/freecodecamp/drums/punchy_kick_1.mp3",
    style: {},
  },
  {
    keyTrigger: "X",
    title: "Side-Stick",
    url: "https://s3.amazonaws.com/freecodecamp/drums/side_stick_1.mp3",
    style: {},
  },
  {
    keyTrigger: "C",
    title: "Snare",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Brk_Snr.mp3",
    style: {},
  },
];

const onStyle = {
  boxShadow: "0 9px 20px rgb(0, 0, 255)",
  animation: "test 10s infinite",
  color: "rgb(0, 0, 255)",
};

const offStyle = {
  boxShadow: "0 9px 20px rgb(74, 74, 75)",
  animation: "none",
  color: "rgb(74, 74, 75)",
};
const pianoKit = (props) => {
  const displayPads = pads.map((pad) => {
    return (
      <button
        key={pad.title}
        className="button"
        style={props.currentPower ? onStyle : offStyle}
        onClick={props.currentPower ? () => {
          new Audio(pad.url).play();
          props.padKeyTrigger(pad.title);
        } : null }
      >
        {pad.keyTrigger}
      </button>
    );
  });

  return <div className="wrapper">{displayPads}</div>;
};

export default pianoKit;
