import React, { Component } from "react";
import "./SwitchButtons.css";
// import HeaterKit from '../HeaterKit/HeaterKit';



class switchButton extends Component{
    state = {
      toggle: true
    }

     toggleHandler = () => {
        if(this.props.enable ){
         let toggle = this.state.toggle;
         this.setState({toggle: !toggle});
         this.props.toggleState(this.state.toggle);
        }   
    }
  
 render(){
     
    let switchPower;
    if (this.props.enable) {
      switchPower = (
        <div className="footer">
          <label className="switch">
            <input type="checkbox" value={this.state.toggle} onChange={this.toggleHandler}/>
            <span className="slider round"></span>
          </label>
          <p className='display'>{this.state.toggle?  this.props.heaterText : this.props.pianoText}</p>
          
        </div>
      );
    } else {
      switchPower = (
        <div className="footer" style={{ border: "1px solid rgb(74, 74, 75)" }}>
          <label className="switch">
            <input type="checkbox" />
            <span
              className="slider round"
              style={{
                border: "1px solid rgb(74, 74, 75)",
                backgroundColor: "rgb(74, 74, 75)",
              }}
            ></span>
          </label>
          
        </div>
      );
    }
    
     return(
        <div>{switchPower}</div>
     )
 }
   
  
  
};

export default switchButton;
