import { Component } from "react";
import "./App.css";
import HeaterKit from "./HeaterKit/HeaterKit";
import SwitcButtons from "../src/SwitchButtons/SwitchButtons";
import PianoKit from "./PianoKit/PianoKit";

class App extends Component {
  state = {
    power: false,
    powerStyle: { animationName: "example" },
    toggle: false,
    padKeyTrigger: "",
  };

  powerHandler = () => {
    let power = this.state.power;
    this.setState({ power: !power, powerStyle: { animationName: "none" } });
  };

  handleCallback = (toggleData) => {
    this.setState({ toggle: toggleData });
  };
  handleKeyTriggers = (keyTrigger) => {
    this.setState({ padKeyTrigger: keyTrigger });
  };

  render() {
    return (
      <div className="App">
        <button className="power" onClick={this.powerHandler} title="click me">
          <i className="fab fa-napster" style={this.state.powerStyle}></i>
        </button>

        <h1 className="title">Drum Machine</h1>

        {this.state.toggle && this.state.power ? (
          <PianoKit
            currentPower={this.state.power}
            padKeyTrigger={this.handleKeyTriggers}
          />
        ) : (
          <HeaterKit
            currentPower={this.state.power}
            padKeyTrigger={this.handleKeyTriggers}
          />
        )}

        <SwitcButtons
          enable={this.state.power}
          heaterText={"Heater Kit | " + this.state.padKeyTrigger}
          pianoText={"Piano Kit | " + this.state.padKeyTrigger}
          toggleState={this.handleCallback}
        />
      </div>
    );
  }
}

export default App;
